<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal('newData')"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="80"></th>
          <th width="80"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openModal('edit',item)"></button>
          </td>
          <td>
            <button class="btn btn-outline-danger btn-sm" @click="delModal('delData',item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :pagination="pagination" @getpagination="getProducts"></pagination>
    <!-- modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i v-if="fileUploading" class="fas fa-spinner fa-spin"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  >
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  alt
                  :src="tempProduct.imageUrl"
                >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      v-model="tempProduct.unit"
                      id="unit"
                      placeholder=""
                    >
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      v-model="tempProduct.origin_price"
                      id="origin_price"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      v-model="tempProduct.price"
                      id="price"
                      placeholder=""
                    >
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    v-model="tempProduct.description"
                    id="description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      v-model="tempProduct.is_enabled"
                      type="checkbox"
                      id="is_enabled"
                      :true-value="1"
                      :false-value="0"
                    >
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updataProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{tempProduct.title}}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="updataProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import pagination from "../pagination";

export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      isNew: "",
      isLoading: false,
      fileUploading: false,
      pagination: {}
    };
  },
  methods: {
    getProducts(item = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/products?page=${item}`;
      const vm = this;
      vm.isLoading = true;
      vm.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      const vm = this;
      if (isNew === "newData") {
        vm.tempProduct = {};
        vm.isNew = "newData";
      } else if (isNew === "edit") {
        vm.tempProduct = { ...item };
        vm.isNew = "edit";
      }
      $("#productModal").modal("show");
    },
    delModal(isNew, item) {
      this.isNew = isNew;
      this.tempProduct = { ...item };
      $("#delProductModal").modal("show");
    },
    updataProduct() {
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/product`;
      const vm = this;
      let httpMethod = "post";
      if (vm.isNew === "edit") {
        httpMethod = "put";
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/product/${vm.tempProduct.id}`;
      } else if (vm.isNew === "delData") {
        httpMethod = "delete";
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/product/${vm.tempProduct.id}`;
      }
      vm.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        console.log(response.data);
        $("#productModal").modal("hide");
        $("#delProductModal").modal("hide");
        vm.getProducts();
      });
    },
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const formData = new FormData();
      const vm = this;
      formData.append("file-to-upload", uploadedFile);
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/upload`;
      vm.fileUploading = true;
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          vm.fileUploading = false;
          if (response.data.success) {
            this.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            this.$bus.$emit("alertmessage", "", "success");
          } else {
            this.$bus.$emit("alertmessage", response.data.message, "danger");
          }
        });
    }
  },
  created() {
    this.getProducts();
  },

  components:{
    pagination,
  }
};
</script>


