<template>
  <div>
    <div class="d-flex justify-content-end">
      <button class="btn btn-primary mt-4" @click="openpostModal('post')"></button>
    </div>
    <table class="table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th class="text-center"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,key) in coupon" :key="key">
          <td>{{item.title}}</td>
          <td>{{item.percent}}%</td>
          <td>{{item.due_date}}</td>
          <td>{{item.code}}</td>
          <td class="d-flex justify-content-center">
            <div>
              <button class="btn btn-outline-primary" @click="openpostModal('put',item)"></button>
              <button class="btn btn-outline-danger" @click="opendelModal('delete',item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- postModal -->
    <div class="modal" id="postModal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-primary">
            <h5 class="modal-title"></h5>
          </div>
          <div class="modal-body">
            <label for="coupon_title"></label>
            <input
              type="text"
              class="form-control"
              id="coupon_title"
              placeholder=""
              v-model="tempcoupon.title"
            >
            <label for="coupon_title"></label>
            <input
              type="text"
              class="form-control"
              id="coupon_percent"
              placeholder=""
              v-model="tempcoupon.percent"
            >
            <label for="coupon_dueDate"></label>
            <input
              type="text"
              class="form-control"
              id="coupon_dueDate"
              placeholder=""
              v-model="tempcoupon.due_date"
            >
            <label for="coupon_code"></label>
            <input
              type="text"
              class="form-control"
              id="coupon_code"
              placeholder=""
              v-model="tempcoupon.code"
            >
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="postCoupon(tempcoupon.id)"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- delmodal -->
    <div class="modal" id="delModal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger">
            <h5 class="modal-title">{{tempcoupon.title}}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p class="text-danger">{{tempcoupon.title}}</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="postCoupon(tempcoupon.id)"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      coupon: [],
      tempcoupon: {},
      couponMethod: ""
    };
  },
  methods: {
    getCoupon(page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupons?page=${page}`;
      const vm = this;
      vm.$http.get(api).then(response => {
        console.log(response);
        vm.coupon = response.data.coupons;
      });
    },
    openpostModal(method, item) {
      const vm = this;
      if (method === "put") {
        vm.couponMethod = "put";
        vm.tempcoupon = { ...item };
      }
      $("#postModal").modal("show");
    },
    opendelModal(method, item) {
      $("#delModal").modal("show");
      this.tempcoupon = { ...item };
      this.couponMethod = "delete";
    },
    postCoupon(id) {
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupon`;
      let httpMethods = "post";
      const vm = this;
      if (vm.couponMethod === "put") {
        httpMethods = "put";
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/coupon/${id}`;
      } else if (vm.couponMethod === "delete") {
        httpMethods = "delete";
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/coupon/${id}`;
      }
      vm.$http[httpMethods](api, {
        data: vm.tempcoupon
      }).then(response => {
        console.log(response.data);
        vm.tempcoupon = response.data;
        vm.getCoupon();
        $("#postModal").modal("hide");
        $("#delModal").modal("hide");
      });
    }
  },
  created() {
    this.getCoupon();
  }
};
</script>


